<template>
  <!--
          luoyiming
          2019-10-25
          
      -->
  <div class="user" v-loading="loading">
    <!---->
    <div class="product-content">
      <!---->
      <div class="common-form mt30"></div>
      <div class="table-wrap">
        <el-row>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.order_no }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.user.nickName }}
              <span>ID({{ detail.user.user_id }})</span>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"> ()</span>
              {{ detail.order_price }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"> ()</span>
              {{ detail.express_price }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"> ()</span>
              <span class="fb orange">{{ detail.pay_price }}</span>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.pay_type.text }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.delivery_type.text }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.order_status.text }}
            </div>
          </el-col>
          <el-col :span="5" v-if="detail['pay_status']['value'] == 10 && detail['order_status']['value'] == 10 && detail['order_source'] == 10">
            <el-button v-if="" type="text" @click="editClick(detail)" size="small"></el-button>
          </el-col>
        </el-row>
      </div>
      <!---->
      <Add v-if="open_edit" :open_edit="open_edit" :order="userModel" @close="closeDialogFunc($event, 'edit')"></Add>

      <!---->
      <div class="common-form mt16"></div>
      <div class="table-wrap">
        <el-table size="small" :data="detail.product" border style="width: 100%">
          <el-table-column prop="product_name" label="" width="400">
            <template slot-scope="scope">
              <div class="product-info">
                <div class="pic"><img v-img-url="scope.row.image.file_path" /></div>
                <div class="info">
                  <div class="name">{{ scope.row.product_name }}</div>
                  <div class="price"> {{ scope.row.product_price }}</div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="product_no" label=""></el-table-column>
          <el-table-column prop="product_weight" label=" (Kg)"></el-table-column>
          <el-table-column prop="total_num" label="">
            <template slot-scope="scope">
              <p>x {{ scope.row.total_num }}</p>
            </template>
          </el-table-column>
          <el-table-column prop="total_price" label="()">
            <template slot-scope="scope">
              <p class="orange fb"> {{ scope.row.total_price }}</p>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div v-if="detail.delivery_type.value == 10">
        <div class="common-form mt16"></div>
        <div class="table-wrap">
          <el-row>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.address.name }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.address.phone }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.address.detail }}
              </div>
            </el-col>
          </el-row>
        </div>
      </div>

      <!---->
      <template v-if="detail.delivery_type.value == 20">
        <div class="common-form mt16"></div>
        <div class="table-wrap" v-if="detail.extract">
          <el-row>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.extract.linkman }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.extract.phone }}
              </div>
            </el-col>
          </el-row>
        </div>
        <div class="common-form mt16"></div>
        <div class="table-wrap" v-if="detail.extract">
          <el-row>
            <el-col :span="5">
              <div class="pb16"><span class="gray9">LOGO</span></div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9">ID</span>
                {{ detail.extract_store.shop_id }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.extract_store.shop_name }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.extract_store.linkman }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.extract_store.phone }}
              </div>
            </el-col>
            <el-col :span="15">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.extract_store.region.province }}- {{ detail.extract_store.region.city }}- {{ detail.extract_store.region.region }}- {{ detail.extract_store.address }}
              </div>
            </el-col>
          </el-row>
        </div>
      </template>

      <!---->
      <div class="table-wrap" v-if="detail.pay_status.value == 20">
        <div class="common-form mt16"></div>
        <div class="table-wrap">
          <el-row>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.pay_price }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.pay_type.text }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.transaction_id }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.pay_status.text }}
              </div>
            </el-col>
            <el-col :span="5">
              <div class="pb16">
                <span class="gray9"></span>
                {{ detail.pay_time }}
              </div>
            </el-col>
          </el-row>
        </div>
      </div>

      <!--   -->
      <div v-if="detail.pay_status.value == 20 && detail.order_status.value == 21">
        <div class="common-form mt16"></div>
        <p></p>
        <el-form size="small" ref="forms" :model="forms" label-width="100px">
          <el-form-item label="">
            <el-radio v-model="forms.is_cancel" :label="1"></el-radio>
            <el-radio v-model="forms.is_cancel" :label="2"></el-radio>
          </el-form-item>

          <el-form-item><el-button type="primary" @click="confirmCancel()"></el-button></el-form-item>
        </el-form>
      </div>

      <!---->

      <div v-if="detail.pay_status.value == 20 && detail.delivery_type.value == 10 && [20, 21].indexOf(detail.order_status.value) === -1">
        <div v-if="detail.delivery_status.value == 10">
          <!--  -->
          <div class="common-form mt16"></div>
          <el-form size="small" ref="form" :model="form" label-width="100px">
            <el-form-item label="">
              <el-select v-model="form.express_id" placeholder="">
                <el-option :label="item.express_name" v-for="(item, index) in expressList" :key="index" :value="item.express_id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=""><el-input v-model="form.express_no" class="max-w460"></el-input></el-form-item>
            <el-form-item><el-button type="primary" @click="onSubmit()"></el-button></el-form-item>
          </el-form>
        </div>
        <div v-else="">
          <div class="common-form mt16"></div>
          <div class="table-wrap">
            <el-row>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ detail.express.express_name }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ detail.express_no }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ detail.delivery_status.text }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ detail.delivery_time }}
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
      </div>
      <!---->
      <div v-if="detail.delivery_type.value == 20 && detail.pay_status.value == 20 && (detail.order_status.value != 21 || detail.order_status.value != 20)">
        <div class="common-form mt16"></div>
        <div v-if="detail.delivery_status.value == 10">
          <el-form size="small" ref="form1" :model="form1" label-width="100px">
            <el-form-item label="">
              <el-select v-model="form1.order.extract_clerk_id" placeholder="">
                <el-option :label="item.real_name" v-for="(item, index) in shopClerkList" :key="item.clerk_id" :value="item.clerk_id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=" "><el-radio v-model="form1.order.extract_status" :label="1"></el-radio></el-form-item>
            <el-form-item><el-button type="primary" @click="onSubmit1(detail.order_id)"></el-button></el-form-item>
          </el-form>
        </div>
        <div v-else class="table-wrap">
          <template v-if="detail.extractClerk">
            <el-row>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ detail.extract_store.shop_name }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ detail.extractClerk.real_name }}
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  <template v-if="detail.delivery_status.value == 20">
                    
                  </template>
                </div>
              </el-col>
              <el-col :span="5">
                <div class="pb16">
                  <span class="gray9"></span>
                  {{ detail.delivery_time }}
                </div>
              </el-col>
            </el-row>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import OrderApi from '@/api/order.js';
import Add from './dialog/Add.vue';
import { deepClone } from '@/utils/base.js';
export default {
  components: {
    Add
  },
  data() {
    return {
      active: 0,
      /**/
      loading: true,
      /**/
      detail: {
        pay_status: [],
        pay_type: [],
        delivery_type: [],
        user: {},
        address: [],
        product: [],
        order_status: [],
        extract: [],
        extract_store: [],
        express: [],
        delivery_status: [],
        extractClerk: []
      },

      /**/
      open_add: false,
      /**/
      pageSize: 20,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1,
      /**/
      form: {},
      forms: {
        is_cancel: 1
      },
      form1: {
        order: {
          extract_status: 1
        }
      },
      order: {},
      delivery_type: 0,
      /**/
      exStyle: [],
      /**/
      shopList: [],
      /**/
      userModel: {},
      /**/
      create_time: '',
      /**/
      expressList: [],
      shopClerkList: [],
      /**/
      open_edit: false
    };
  },
  created() {
    /**/
    this.getParams();
  },
  methods: {
    next() {
      if (this.active++ > 4) this.active = 0;
    },
    /**/
    getParams() {
      let self = this;
      // 
      const params = this.$route.query.order_id;
      OrderApi.orderdetail(
        {
          order_id: params
        },
        true
      )
        .then(data => {
          self.loading = false;
          self.detail = data.data.detail;
          self.expressList = data.data.expressList;
          self.shopClerkList = data.data.shopClerkList;
        })
        .catch(error => {
          self.loading = false;
        });
    },
    /**/
    onSubmit() {
      let self = this;
      let param = self.form;
      let order_id = this.$route.query.order_id;
      OrderApi.delivery(
        {
          param: param,
          order_id: order_id
        },
        true
      )
        .then(data => {
          self.loading = false;
          self.$message({
            message: '',
            type: 'success'
          });
          self.getParams();
        })
        .catch(error => {
          self.loading = false;
        });
    },
    /**/
    confirmCancel() {
      let self = this;
      let order_id = this.$route.query.order_id;
      let is_cancel = self.forms.is_cancel;
      OrderApi.confirm(
        {
          order_id: order_id,
          is_cancel: is_cancel
        },
        true
      )
        .then(data => {
          self.loading = false;
          self.$message({
            message: '',
            type: 'success'
          });
          self.getParams();
        })
        .catch(error => {
          self.loading = false;
        });
    },
    /**/
    onSubmit1(e) {
      let self = this;
      let form1 = self.form1;
      form1.order_id = e;
      OrderApi.extract(
        {
          form1
        },
        true
      )
        .then(data => {
          self.loading = false;
          self.$message({
            message: '',
            type: 'success'
          });
          self.getParams();
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    editClick(item) {
      //                this.userModel = item;
      this.userModel = deepClone(item);
      this.open_edit = true;
    },
    /**/
    closeDialogFunc(e, f) {
      if (f == 'edit') {
        this.open_edit = e.openDialog;
        this.getParams();
      }
    }
  }
};
</script>
<style></style>



