<template>
  <!--
      luoyiming
      2019-10-25
      -
  -->
  <div class="user" v-loading="loading">
    <div class="product-content">
      <!---->
      <div class="common-form"></div>
      <div class="refund-detail-content">
        <el-row>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.order_master.order_no }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.user.nickName }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.type.text }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.status.text }}
            </div>
          </el-col>
          <el-col :span="4">
            <div class="pb16"><el-button size="mini" href="/" target="_blank" @click="addClick(detail)"></el-button></div>
          </el-col>
        </el-row>
      </div>
      <!---->
      <div class="common-form"></div>
      <div class="refund-detail-content">
        <el-row>
          <el-col :span="5">
            <div class="pb16"><span class="gray9"></span></div>
          </el-col>
          <el-col :span="19">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.orderproduct.product_name }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9">(Kg)</span>
              {{ detail.orderproduct.product_weight }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.orderproduct.line_price }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.orderproduct.total_num }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.orderproduct.total_pay_price }}
            </div>
          </el-col>
        </el-row>
      </div>

      <!---->
      <div>
        <div class="common-form"></div>
        <div class="apply-reason">{{ detail.apply_desc }}</div>
        <div v-if="detail.image.length > 0" class="pic" v-for="(item, index) in detail.image" :key="index"><img v-img-url="item.file_path" alt="" width="60" /></div>
      </div>

      <!---->
      <div v-if="detail.is_agree.value == 0">
        <div class="common-form mt16"></div>
        <el-form size="small" ref="form" :model="form" label-width="80px">
          <el-form-item label="">
            <span class="orange">{{ detail.type.text }}</span>
          </el-form-item>
          <el-form-item label=" ">
            <el-radio-group v-model="form.is_agree">
              <el-radio :label="10"></el-radio>
              <el-radio :label="20"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label=" ">
            <el-select v-model="form.address_id" placeholder="">
              <el-option v-for="(item, index) in address" :key="index" :label="item.detail" :value="item.address_id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item><el-button type="primary" @click="onSubmit(detail.order_refund_id)"></el-button></el-form-item>
        </el-form>
      </div>

      <!--  -->
      <div v-if="detail.is_agree.value == 10">
        <div class="common-form mt16"></div>
        <el-row>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.address.name }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.address.phone }}
            </div>
          </el-col>
          <el-col :span="14">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.address.detail }}
            </div>
          </el-col>
        </el-row>
      </div>

      <!--  -->
      <div v-if="detail.is_agree.value == 20">
        <div class="common-form"></div>
        <div class="apply-reason">{{ detail.refuse_desc }}</div>
      </div>

      <!--  -->
      <div v-if="detail.type.value == 10 && detail.is_agree.value == 10 && detail.is_user_send == 1">
        <div class="common-form mt16"></div>
        <el-row>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.express.express_name }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.express_no }}
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              
            </div>
          </el-col>
          <el-col :span="5">
            <div class="pb16">
              <span class="gray9"></span>
              {{ detail.send_time }}
            </div>
          </el-col>
          <el-col :span="4">
            <div class="pb16">
              <span class="gray9"></span>
              <block v-if="detail.is_receipt == 1"></block>
              <block v-else=""></block>
            </div>
          </el-col>
        </el-row>
      </div>

      <!--  -->
      <div v-if="detail.type.value == 10 && detail.is_agree.value == 10 && detail.is_user_send == 1 && detail.is_receipt == 0">
        <div class="common-form mt16"></div>
        <el-form size="small" ref="money" :model="money" label-width="80px">
          <p> </p>
          <el-form-item label="">
            <span class="orange">{{ detail.type.text }}</span>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="money.refund_money"></el-input>
            <p>{{ detail.orderproduct.total_pay_price }} </p>
          </el-form-item>
          <el-form-item><el-button type="primary" @click="refundMoney(detail.order_refund_id)"></el-button></el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import OrderApi from '@/api/order.js';
export default {
  data() {
    return {
      tableData: {},
      /**/
      detail: {
        is_agree: {},
        type: {},
        status: {},
        order_master: {},
        user: {},
        orderproduct: {},
        express: {},
        address: {}
      },
      /**/
      order: {},
      /**/
      address: {},
      form: {
        is_agree: 10,
        address_id: '',
        refuse_desc: ''
      },
      money: {
        refund_money: 0
      },
      order_refund_id: 0,
      loading: true
    };
  },
  created() {
    /**/
    this.getData();
  },
  methods: {
    /**/

    getData() {
      let self = this;
      let order_refund_id = this.$route.query.order_refund_id;
      OrderApi.refundDetail(
        {
          order_refund_id: order_refund_id
        },
        true
      )
        .then(data => {
          self.loading = false;
          self.detail = data.data.detail;
          self.order = data.data.order;
          self.address = data.data.address;
        })
        .catch(error => {});
    },
    /**/
    addClick(row) {
      let self = this;
      let params = row.order_id;
      this.$router.push({
        path: '/order/refund/Order',
        query: {
          order_id: params
        }
      });
    },
    /**/
    onSubmit(e) {
      let self = this;
      let form = self.form;
      let order_refund_id = e;
      OrderApi.Approval(
        {
          is_agree: form.is_agree,
          address_id: form.address_id,
          refuse_desc: form.refuse_desc,
          order_refund_id: order_refund_id
        },
        true
      )
        .then(data => {
          self.$message({
            message: '',
            type: 'success'
          });
          this.getData();
        })
        .catch(error => {});
    },
    /**/
    refundMoney(e) {
      let self = this;
      let form = self.money;
      OrderApi.receipt(
        {
          refund_money: form.refund_money,
          order_refund_id: e
        },
        true
      )
        .then(data => {
          self.$message({
            message: '',
            type: 'success'
          });
          this.getData();
        })
        .catch(error => {});
    }
  }
};
</script>
<style>
.apply-reason {
  padding: 16px;
  background: #fff4f4;
  border: 1px solid #f6e6e6;
}
</style>



