<template>
    <!--
            wangxw
            2019-11-05
            diy 
        -->
    <div>
        <div class="common-form">
            <span>{{ curItem.name }}</span>
        </div>
        <el-form size="small" :model="curItem" label-width="100px">
            <!-- <el-form-item label="">
                    <el-radio-group v-model="curItem.style.btnShape">
                        <el-radio :label="3"></el-radio>
                        <el-radio :label="6"></el-radio>
                        <el-radio :label="9"></el-radio>
                      </el-radio-group>
                  </el-form-item> -->
            <el-form-item label="">
                <div class="d-s-c">
                    <el-color-picker v-model="curItem.style.btnColor"></el-color-picker>
                    <el-button type="button" style="margin-left: 10px;"
                               @click.stop="$parent.onEditorResetColor(curItem.style, 'btnColor', '#ffffff')">
                    </el-button>
                </div>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="curItem.params.interval"></el-input>
                <p></p>
            </el-form-item>
            <el-form-item label="">
                <div class="param-img-item" :key="index" v-for="(banner, index) in curItem.data">
                    <div class="delete-box"><i class="el-icon-delete-solid"
                                               @click="$parent.onEditorDeleleData(index, selectedIndex)"></i></div>
                    <div class="pic"><img v-img-url="banner.imgUrl" alt=""
                                          @click="$parent.onEditorSelectImage(banner, 'imgUrl')"/></div>
                    <p class="tc gray9">750x290</p>
                    <div class="d-s-c">
                        <div class="url-box flex-1">
                            <span class="key-name"></span>
                            <el-input placeholder="" v-model="banner.name"></el-input>
                        </div>
                        <div class="url-box ml10">
                            <el-button type="primary" @click="changeLink(index)"></el-button>
                        </div>
                    </div>
                </div>
                <div class="d-c-c">
                    <el-button @click="$parent.onEditorAddData"></el-button>
                </div>
            </el-form-item>
        </el-form>
        <Setlink v-if="is_linkset" :is_linkset="is_linkset" @closeDialog="closeLinkset"></Setlink>
    </div>
</template>

<script>
    import Setlink from '@/components/setlink/Setlink';
    export default {
        components: {
            Setlink
        },
        data() {
            return {
                /**/
                is_linkset: false,
                index: null
            };
        },
        props: ['curItem', 'selectedIndex'],
        methods: {
            /**/
            changeLink(index) {
                this.is_linkset = true;
                this.index = index;
            },
            /**/
            closeLinkset(e) {
//                console.log(e);
                this.is_linkset = false;
                this.curItem.data[this.index].linkUrl = e.url;
                this.curItem.data[this.index].name = '' + ' ' + e.type + ' ' + e.name;
            }
        }
    };
</script>

<style></style>



