<template>
    <el-select v-model="activePage" placeholder="" class="percent-w100" @change="changeFunc" value-key="url">
        <el-option v-for="item in pages" :key="item.url" :label="item.name" :value="item"></el-option>
    </el-select>
</template>

<script>
    export default{
        data(){
            return {
                /**/
                pages: [
                    {
                        url: 'pages/index/index',
                        type: '',
                        name: ''
                    },
                    {
                        url: 'pages/product/category',
                        name: '',
                        type: '',
                    },
                    {
                        url: 'pages/cart/cart',
                        name: '',
                        type: '',
                    },
                    {
                        url: 'pages/user/index/index',
                        name: '',
                        type: '',
                    }],
                /**/
                activePage: {}
            }
        },
        methods: {
            /**/
            changeFunc(e){
                this.$emit('changeData', e);
            }
        }
    }
</script>

<style>
</style>



