<template>
  <!--
      luoyiming
      2019-10-25
      
  -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="250px">
      <!---->
      <div class="common-form"></div>

      <el-form-item label="AppID ID" :rules="[{ required: true, message: 'AppID ID' }]" prop="wxapp_id">
        <el-input v-model="form.wxapp_id" class="max-w460"></el-input>
      </el-form-item>
      <el-form-item label="AppSecret " :rules="[{ required: true, message: 'AppSecret ' }]" prop="wxapp_secret">
        <el-input v-model="form.wxapp_secret" type="password" class="max-w460"></el-input>
      </el-form-item>

      <div class="common-form"></div>

      <el-form-item label=" MCHID">
        <el-input v-model="form.mchid" class="max-w460"></el-input>
      </el-form-item>
      <el-form-item label=" APIKEY ">
        <el-input v-model="form.apikey" class="max-w460"></el-input>
      </el-form-item>

      <el-form-item label="apiclient_cert.pem">
        <el-input type="textarea" :rows="4" placeholder="apiclient_cert.pem" v-model="form.cert_pem" class="max-w460"></el-input>
        <div class="tips">apiclient_key.pem</div>
      </el-form-item>
      <el-form-item label="apiclient_key.pem">
        <el-input type="textarea" :rows="4" placeholder="apiclient_cert.pem" v-model="form.key_pem" class="max-w460"></el-input>
        <div class="tips">apiclient_key.pem</div>
      </el-form-item>

      <!---->
      <div class="common-button-wrapper"><el-button size="small" type="primary" @click="onSubmit"></el-button></div>
    </el-form>
  </div>
</template>

<script>
import AppSettingApi from '@/api/appsetting.js';

export default {
  data() {
    return {
      /**/
      // activeIndex: '1',
      /*form*/
      form: {}
    };
  },
  created() {
    this.getData();
  },

  methods: {
    getData() {
      let self = this;
      AppSettingApi.appwxDetail({}, true)
        .then(data => {
          if (data.data.data != null) {
            self.form = data.data.data;
          }
        })
        .catch(error => {});
    },
    //
    onSubmit() {
      let self = this;
      let params = this.form;
      AppSettingApi.editAppWx(params, true)
        .then(data => {
          self.$message({
            message: '',
            type: 'success'
          });
          self.$router.push('/appsettings/appwx');
        })
        .catch(error => {});
    }
  }
};
</script>

<style>
.tips {
  color: #ccc;
}
</style>



