<template>
  <!--
          wangxw
          2019-10-26
          
      -->
  <div v-loading="loading" class="diy-container clearfix">
    <div class="diy-menu">
      <div class="common-form"></div>
      <div class="min-group">
        <div class="hd"></div>
        <div class="bd">
          <div class="item" @click="onAddItem('banner')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
          <div class="item" @click="onAddItem('imageSingle')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
          <div class="item" @click="onAddItem('window')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
          <div class="item" @click="onAddItem('video')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
        </div>
      </div>
      <div class="min-group">
        <div class="hd"></div>
        <div class="bd">
          <div class="item" @click="onAddItem('search')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
          <div class="item" @click="onAddItem('notice')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
          <div class="item" @click="onAddItem('navBar')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
          <div class="item" @click="onAddItem('product')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
        </div>
      </div>
      <div class="min-group">
        <div class="hd"></div>
        <div class="bd">
          <div class="item" @click="onAddItem('service')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
          <div class="item" @click="onAddItem('richText')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
          <div class="item" @click="onAddItem('blank')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
          <div class="item" @click="onAddItem('guide')">
            <p class="p-icon">
              <Icon :iconname="'#icon-tuichu'"></Icon>
            </p>
            <p class="p-txt"></p>
          </div>
        </div>
      </div>
    </div>

    <!--diy-->
    <div class="diy-phone">
      <!---->
      <Model ref="model" :form="form" :opts="opts" :defaultData="defaultData" :diyData="diyData"></Model>
    </div>

    <!---->
    <div class="diy-info">
      <Params :form="form" :opts="opts" :defaultData="defaultData" :diyData="diyData"></Params>
    </div>

    <!---->
    <div class="common-button-wrapper">
      <el-button size="small" type="primary" @click="Submit()" :loading="loading"></el-button>
    </div>
  </div>
</template>

<script>
  import PageApi from '@/api/page.js';
  import Model from './diy/Model.vue';
  import Params from './diy/Params.vue';
  export default {
    components: {
      /**/
      Model,
      /**/
      Params
    },
    data() {
      return {
        /**/
        loading: true,
        page_id: 0,
        defaultData: {},
        diyData: {},
        opts: {},
        form: {
          umeditor: {},
          curItem: {
          },
          // 
          selectedIndex: -1
        }
      };
    },
    created() {
      /**/
      this.getData();
    },
    methods: {
      /**/
      getData() {
        let self = this;
        PageApi.editPage({}, true).then(data => {
          self.loading = false;
          self.defaultData = data.data.defaultData;
          self.diyData = data.data.jsonData;
          self.opts = data.data.opts;
        }).catch(error => {
          self.loading = false;
        });
      },
      /**
       * Diy
       * @param key
       */
      onAddItem: function(key) {
        let self = this;
        // diy
        var data = self.defaultData[key];
        self.diyData.items.push(data);
        // 
        self.$refs.model.onEditer(self.diyData.items.length - 1);
      },
      /**/
      Submit() {
        let self = this;
        self.loading = true;
        let Parmens = self.diyData;
        let page_id = self.page_id;
        PageApi.SavePage({
          Parmens: Parmens,
          page_id: page_id
        }, true).then(data => {
          self.loading = false;
          self.$message({
            message: '',
            type: 'success'
          });
          self.getData();
        }).catch(error => {
          self.loading = false;
        });
      }
    }
  };
</script>

<style lang="scss">
  @import '@/styles/page/diy.scss';

</style>



