<template>
  <!--
       WuYuseng
      2019-10-26
      --
  -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="150px">
      <!---->
      <div class="common-form"></div>
      <el-form-item label="AccessKey" :rules="[{ required: true, message: 'AccessKey' }]" prop="AccessKeyId">
        <el-input v-model="form.AccessKeyId" class="max-w460"></el-input>
      </el-form-item>
      <el-form-item label="AccessKeySecret" :rules="[{ required: true, message: 'AccessKeySecret' }]" prop="AccessKeySecret">
        <el-input v-model="form.AccessKeySecret" class="max-w460"></el-input>
      </el-form-item>
      <el-form-item label="" :rules="[{ required: true, message: '' }]" prop="sign"><el-input v-model="form.sign" class="max-w460"></el-input></el-form-item>
      <div class="common-form"></div>
      <el-form-item label="" :rules="[{ required: true, message: '' }]" prop="accept_phone">
        <el-input v-model="form.accept_phone" class="max-w460"></el-input>
      </el-form-item>

      <!---->
      <div class="common-button-wrapper"><el-button type="primary" size="small" @click="onSubmit" :loading="loading"></el-button></div>
    </el-form>
    
  </div>
</template>

<script>
import SettingApi from '@/api/setting.js';

export default {
  data() {
    return {
      /**/
      // activeIndex: '1',
      /*form*/
      form: {
        AccessKeyId: '',
        AccessKeySecret: '',
        sign: '',
        accept_phone: ''
      },
      loading: false
    };
  },
  created() {
    this.getData();
  },

  methods: {
    getData() {
      let self = this;
      SettingApi.smsDetail({}, true)
        .then(data => {
          let vars = data.data.vars.values;
          self.form.AccessKeyId = vars.engine.aliyun.AccessKeyId;
          self.form.AccessKeySecret = vars.engine.aliyun.AccessKeySecret;
          self.form.sign = vars.engine.aliyun.sign;
          self.form.accept_phone = vars.engine.aliyun.accept_phone;
        })
        .catch(error => {});
    },
    //
    onSubmit() {
      let self = this;
      self.loading = true;
      let params = this.form;
      self.$refs.form.validate(valid => {
        if (valid) {
          SettingApi.editSms(params, true)
            .then(data => {
              self.loading = false;
              self.$message({
                message: '',
                type: 'success'
              });
            })
            .catch(error => {
              self.loading = false;
            });
        }
      });
    },
    
    //
    sendOut() {
      let self = this;
      let params = this.form;
      SettingApi.sendSms(params, true)
        .then(data => {
          self.$message({
            message: '',
            type: 'success'
          });
          self.$router.push('/setting/Sms');
        })
        .catch(error => {});
    }
  }
};
</script>

<style>
.tips {
  color: #ccc;
}
</style>



