<template>
  <!--
          luoyiming
          2019-10-26
          
      -->
  <div class="product-add" v-loading="loading">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="100px">
      <!---->
      <div class="common-form"></div>
      <el-form-item label="">
        <p>{{ form.user.nickName }}</p>
      </el-form-item>
      <el-form-item label="">
        <p>{{ form.product.product_name | isNull }}</p>
      </el-form-item>
      <el-form-item label="">
        <img :src="path" width="120px;" />
      </el-form-item>
      <el-form-item label="">
        <p>{{ form.create_time }}</p>
      </el-form-item>
      <el-form-item label="">
        <div v-if="form.image.length > 0" v-for="(item, index) in form.image" :key="index"><img v-img-url="item.file_path" alt="" width="60" /></div>
      </el-form-item>
      <el-form-item label="" v-model="form.score">
        <p v-if="form.score == 10"></p>
        <p v-if="form.score == 20"></p>
        <p v-if="form.score == 30"></p>
      </el-form-item>
      <el-form-item label="">
        <div>{{ form.content }}</div>
      </el-form-item>
      <el-form-item label="">
        <p>{{ form.sort }}</p>
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="form.status">
          <el-radio :label="1"></el-radio>
          <el-radio :label="2"></el-radio>
        </el-radio-group>
      </el-form-item>
      <!---->
      <div class="common-button-wrapper">
        <el-form-item><el-button type="primary" @click="onSubmit"></el-button></el-form-item>
      </div>
    </el-form>
  </div>
</template>

<script>
import PorductApi from '@/api/product.js';
export default {
  components: {
  },
  data() {
    return {
      is_comment: false,
      /**/
      path: '',
      /**/
      comment_img: '',
      /*form*/
      form: {
        user: {},
        product: {}
      },
      loading: true
    };
  },
  created() {
    /**/
    this.getComment();
  },
  methods: {

    /**/
    getComment() {
      let self = this;
      let comment_id = this.$route.query.comment_id;
      PorductApi.getComment(
        {
          comment_id: comment_id
        },
        true
      )
        .then(data => {
          self.loading = false;
          self.form = data.data.data;
          self.path = data.data.data.product.image[0].file_path;
          self.comment_id = data.data.data.comment_id;
        })
        .catch(error => {});
    },

    /**/
    onSubmit() {
      let self = this;
      let params = {
        comment_id:this.form.comment_id,
        status:this.form.status
      }
      PorductApi.editComment(params, true)
        .then(data => {
          if (data.code == 1) {
            self.$message({
              message: '',
              type: 'success'
            });
            self.$router.push('/product/comment/Evaluation');
          } else {
            self.$message.error('');
          }
        })
        .catch(error => {});
    }
  }
};
</script>

<style scoped="">
.edit_container {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.ql-editor {
  height: 400px;
}
</style>



