<template>
 <!--
       WuYuseng
      2019-10-26
      --
  -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="200px">
      <!---->
      <div class="common-form"></div>

      <el-form-item label="AccessKe">
        <el-input v-model="form.AccessKeyId" class="max-w460"></el-input>
      </el-form-item>
      <el-form-item label="AccessKeySecret">
        <el-input v-model="form.AccessKeySecret" class="max-w460"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.sign" class="max-w460"></el-input>
      </el-form-item>

      <div class="common-form"></div>
      <el-form-item label="">
        <el-radio-group v-model="form.is_enable">
          <el-radio :label="1"></el-radio>
          <el-radio :label="0"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="ID Template Code">
        <el-input v-model="form.template_code" class="max-w460"></el-input>
        <div class="tips">SMS_139800030</div>
        <div class="tips">${order_no}</div>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.accept_phone" class="max-w460"></el-input>
        <div class="tips"> , </div>
        <div class="tips"><el-link type="primary" :underline="false" @click="sendOut"></el-link>
        </div>
      </el-form-item>

      <!---->
      <div class="common-button-wrapper">
          <el-button type="primary" @click="onSubmit"></el-button>
      </div>
    </el-form>


  </div>

</template>

<script>
  import SettingApi from '@/api/setting.js';

  export default {
    data() {
      return {
        /**/
        // activeIndex: '1',
        /*form*/
        form: {
          AccessKeyId: '',
          AccessKeySecret: '',
          sign: '',
          is_enable: '',
          template_code: '',
          accept_phone: '',
          msg_type: 'order_pay'
        },


      };
    },
    created() {
      this.getData()
    },

    methods: {
      getData() {
        let self = this;
        SettingApi.smsDetail({}, true)
          .then(data => {
            let vars = data.data.vars.values;
            self.form.AccessKeyId = vars.engine.aliyun.AccessKeyId;
            self.form.AccessKeySecret = vars.engine.aliyun.AccessKeySecret;
            self.form.sign = vars.engine.aliyun.sign;
            self.form.accept_phone = vars.engine.aliyun.accept_phone;
          })
          .catch(error => {});

      },
      //
      onSubmit() {
        let self = this;
        let params = this.form;
        SettingApi.editSms(params, true)
          .then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.$router.push('/setting/Sms');

          })
          .catch(error => {

          });
      },
      //
      sendOut() {
        let self = this;
        let params = this.form;
        SettingApi.sendSms(params, true)
          .then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.$router.push('/setting/Sms');

          })
          .catch(error => {

          });
      }



    }

  };
</script>

<style>
  .tips {
    color: #ccc;
  }
</style>



