<template>
  <!--
    	wangxw
    	2019-10-26
    	--
    -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="200px">
      <!---->
      <div class="common-form"></div>
      <el-form-item label="" :rules="[{ required: true, message: '' }]" prop="name">
        <el-input type="text" v-model="form.name" class="max-w460" prop="name"></el-input>
      </el-form-item>
      <el-form-item label="" :rules="[{ required: true, message: '' }]" prop="phone">
        <el-input type="text" v-model="form.phone" class="max-w460"></el-input>
      </el-form-item>
      <el-form-item label="" :rules="[{ required: true, message: '' }]" prop="detail">
        <el-input type="text" v-model="form.detail" class="max-w460"></el-input>
      </el-form-item>
      <el-form-item label="" :rules="[{required: true,message: ''}]" prop="sort">
        <el-input v-model="form.sort" type="number" class="max-w460"></el-input>
        <div class="tips"></div>
      </el-form-item>
      <!---->
      <div class="common-button-wrapper">
        <el-button size="small" type="primary" @click="onSubmit()" :loading="loading"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import SettingApi from '@/api/setting.js';

  export default {
    data() {
      return {
        loading: false,
        /**/
        // activeIndex: '1',
        /*form*/
        form: {
          name: '',
          phone: '',
          detail: '',
          sort: 1
        }
      };
    },
    created() {},

    methods: {
      //
      onSubmit() {
        let self = this;
        let form = self.form;
        self.$refs.form.validate(valid => {
          if (valid) {
            self.loading = true;
            SettingApi.addAddress(form, true)
              .then(data => {
                self.loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.$router.push('/setting/address/index');
              })
              .catch(error => {
                self.loading = false;
              });
          }
        });
      }
    }
  };
</script>

<style>
  .tips {
    color: #ccc;
  }
</style>



