<template>
  <!--
          luoyiming
          2019-10-25
          
      -->
  <div class="user">
    <!---->
    <div class="common-seach-wrap">
      <div class="common-form"></div>
      <el-form size="small" :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="formInline.search" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="formInline.rechargeType" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option v-for="(item,index) in rechargeType" :key="index" :label="item.name" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="formInline.pay_status" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option v-for="(item,index) in pay_status" :key="index" :label="item.name" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div class="block">
            <span class="demonstration"></span>
            <el-date-picker v-model="formInline.value1" type="daterange" range-separator="" start-placeholder=""
              end-placeholder="">
            </el-date-picker>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="user_id" label="ID" width="80"></el-table-column>
          <el-table-column prop="user.nickName" label="">
            <template slot-scope="scope">
              <p>{{scope.row.user.nickName}}</p>
              <p>(ID{{scope.row.user.user_id}})</p>
            </template>
          </el-table-column>
          <el-table-column prop="user.nickName" label="">
            <template slot-scope="scope">
              <img :src="scope.row.user.avatarUrl" width="60" />
            </template>
          </el-table-column>
          <el-table-column prop="scene.text" label="">
            <template slot-scope="scope">
              <span style="background-color: #3bb4f2;color: #fff">{{scope.row.scene.text}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="money" label="	">
            <template slot-scope="scope">
              <p v-if="scope.row.money >0">+{{scope.row.money}}</p>
              <p v-else="">{{scope.row.money}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="describe" label=""></el-table-column>
          <el-table-column prop="remark" label="">
            <template slot-scope="scope">
              <p v-if="scope.row.remark ==''">--</p>
              <p v-else="">{{scope.row.remark}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label="" width="140"></el-table-column>
          <el-table-column prop="create_time" label="" width="140"></el-table-column>
          <el-table-column prop="create_time" label="	" width="140"></el-table-column>
          <el-table-column prop="create_time" label="	" width="140"></el-table-column>
          <el-table-column prop="create_time" label="	" width="140"></el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
          :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber">
        </el-pagination>
      </div>
    </div>


  </div>
</template>

<script>
  import UserApi from '@/api/user.js';
  export default {

    data() {
      return {
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        formInline: {
          nick_name: '',
          scene: '',
          value1: ''
        },
        /**/
        pay_status: [],
        rechargeType: [],
        /**/
        value1: '',
      };
    },
    created() {

      /**/
      this.getTableList();

    },
    methods: {

      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        self.getTableList();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getTableList();
      },

      /**/
      getTableList() {
        let self = this;
        let Params = self.formInline;
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        UserApi.RechargeOrder(Params, true).then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.totalDataNumber = data.data.list.total;
          self.pay_status = data.data.attributes.pay_status;
          self.rechargeType = data.data.attributes.rechargeType;
        }).catch(error => {

        });
      },

      /**/
      onSubmit() {
        let self = this;
        self.loading = true;
        let Params = self.formInline;
        self.getTableList();
      },


      /**/
      closeDialogFunc(e, f) {
        if (f == 'add') {
          this.open_add = e.openDialog;
          if (e.type == 'success') {
            this.getTableList();
          }
        }
        if (f == 'edit') {
          this.open_edit = e.openDialog;
          if (e.type == 'success') {
            this.getTableList();
          }
        }
      },


    }
  };
</script>
<style></style>



