<template>
  <!--
          luoyiming
          2019-10-25
          
      -->
  <div class="user">
    <el-steps :active="active" finish-status="success">
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
    </el-steps>
    <!---->
    <div class="product-content">
      <!---->
      <div class="common-form mt50"></div>
      <div class="table-wrap">
        <el-table :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="order_no" label=""></el-table-column>
          <el-table-column prop="user.nickName" label="">
            <template slot-scope="scope">
              <div>{{ scope.row.user.nickName }}</div>
              <div class="gray9">ID({{ scope.row.user.user_id }})</div>
            </template>
          </el-table-column>
          <el-table-column prop="order_price" label=" ()">
            <template slot-scope="scope">
              <p>{{ scope.row.order_price }}</p>
              <p>+{{ scope.row.express_price }}</p>
              <p>{{ scope.row.pay_price }}</p>
            </template>
          </el-table-column>
          <el-table-column prop="pay_type.text" label=""></el-table-column>
          <el-table-column prop="delivery_type.text" label=""></el-table-column>
          <el-table-column prop="delivery_status.text" label="">
            <template slot-scope="scope">
              <p>{{ scope.row.pay_status.text }}</p>
              <p>{{ scope.row.delivery_status.text }}</p>
              <p>{{ scope.row.receipt_status.text }}</p>
              <p>{{ scope.row.order_status.text }}</p>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="common-form mt50"></div>
      <div class="table-wrap">
        <el-table :data="productList" border style="width: 100%" v-loading="loading">
          <el-table-column prop="product_name" label="" width="400">
            <template slot-scope="scope">
              <div class="product-info">
                <div class="pic"><img v-img-url="scope.row.image.file_path" /></div>
                <div class="info">
                  <div class="name">{{ scope.row.product_name }}</div>
                  <div class="price"> {{ scope.row.product_price }}</div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="product_no" label=""></el-table-column>
          <el-table-column prop="product_weight" label=" (Kg)"></el-table-column>
          <el-table-column prop="total_num" label="">
            <template slot-scope="scope">
              <p>x {{ scope.row.total_num }}</p>
            </template>
          </el-table-column>
          <el-table-column prop="total_price" label="()">
            <template slot-scope="scope">
              <p> {{ scope.row.total_price }}</p>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div v-if="delivery_type == 10">
        <div class="common-form mt50"></div>
        <div class="table-wrap">
          <el-table :data="address" border style="width: 100%" v-loading="loading">
            <el-table-column prop="name" label="" width="200"></el-table-column>
            <el-table-column prop="phone" label="" width="300"></el-table-column>
            <el-table-column prop="detail" label=""></el-table-column>
          </el-table>
        </div>
      </div>

      <!---->
      <div v-if="delivery_type == 20">
        <div class="common-form mt50"></div>
        <div class="table-wrap">
          <el-table :data="extract_store" border style="width: 100%" v-loading="loading">
            <el-table-column prop="shop_id" label="ID" width="200"></el-table-column>
            <el-table-column prop="phone" label="logo" width="300">
              <template slot-scope="scope">
                <!--<img :src="scope.row.logo.file_path" width="50" height="50"/>-->
              </template>
            </el-table-column>
            <el-table-column prop="shop_name" label="" width="300"></el-table-column>
            <el-table-column prop="linkman" label="" width="300"></el-table-column>
            <el-table-column prop="phone" label="" width="300"></el-table-column>
            <el-table-column prop="address" label=""></el-table-column>
          </el-table>
        </div>
      </div>

      <!---->
      <div class="common-form mt50"></div>
      <div class="table-wrap">
        <el-table :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="pay_price" label="" width="300">
            <template slot-scope="scope">
              <p> {{ scope.row.pay_price }}</p>
            </template>
          </el-table-column>
          <el-table-column prop="pay_type.text" label="" width="300"></el-table-column>
          <el-table-column prop="transaction_id" label="" width="300">
            <template slot-scope="scope">
              <p v-if="scope.row.transaction_id == ''">--</p>
              <p v-else="">{{ scope.row.transaction_id }}</p>
            </template>
          </el-table-column>
          <el-table-column prop="pay_status.text" label=""></el-table-column>
          <el-table-column prop="pay_time" label="">
            <template slot-scope="scope">
              <p v-if="scope.row.pay_time != 0">{{ scope.row.pay_time }}</p>
              <p v-else="">--</p>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div v-if="delivery_type == 10">
        <!---->
        <div v-if="tableData[0].delivery_status.value == 10 && tableData[0].pay_status.value == 20">
          <div class="common-form mt50"></div>
          <el-form size="small" ref="form" :model="form" label-width="100px">
            <el-form-item label=""><el-input v-model="form.express_no" class="max-w460"></el-input></el-form-item>
            <el-form-item label="">
              <el-select v-model="form.express_id" placeholder="">
                <el-option :label="item.express_name" v-for="(item, index) in expressList" :key="index" :value="item.express_id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item><el-button type="primary" @click="onSubmit()"></el-button></el-form-item>
          </el-form>
        </div>
        <div v-else>
          <div class="common-form mt50"></div>
          <div class="table-wrap">
            <el-table :data="tableData" border style="width: 100%" v-loading="loading">
              <el-table-column prop="express.express_name" label="" width="300"></el-table-column>
              <el-table-column prop="express_no" label="" width="300"></el-table-column>
              <el-table-column prop="delivery_status.text" label="" width="300"></el-table-column>
              <el-table-column prop="delivery_time" label=""></el-table-column>
            </el-table>
          </div>
        </div>
      </div>
    </div>
    <!---->
    <div v-if="delivery_type == 20">
      <div class="common-form mt50"></div>
      <div class="table-wrap">
        <el-table :data="extract_store" border style="width: 100%" v-loading="loading">
          <el-table-column prop="shop_name" label="" width="300"></el-table-column>
          <el-table-column prop="pay_type.text" label="" width="300"></el-table-column>
          <el-table-column prop="delivery_status.text" label="" width="300"></el-table-column>
          <el-table-column prop="delivery_time" label=""></el-table-column>
        </el-table>
      </div>
    </div>
    <div v-if="tableData[0].pay_status.value == 20 && tableData[0].order_status.value == 21">
      <div class="common-form mt50"></div>
      <p></p>
      <el-form size="small" ref="forms" :model="forms" label-width="100px">
        <el-form-item label="">
          <el-radio v-model="forms.is_cancel" :label="1"></el-radio>
          <el-radio v-model="forms.is_cancel" :label="2"></el-radio>
        </el-form-item>

        <el-form-item><el-button type="primary" @click="confirmCancel()"></el-button></el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import OrderApi from '@/api/order.js';
export default {
  data() {
    return {
      active: 0,
      /**/
      loading: true,
      /**/
      tableData: [],
      productList: [],
      address: [],
      extract_store: [],
      express: [],
      /**/
      pageSize: 20,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1,
      /**/
      form: {},
      forms: {
        is_cancel: 1
      },
      delivery_type: 0,
      /**/
      exStyle: [],
      /**/
      shopList: [],
      /**/
      userModel: {},
      /**/
      create_time: '',
      /**/
      expressList: []
    };
  },
  created() {
    /**/
    this.getParams();
  },
  methods: {
    /**/
    handleCurrentChange(val) {
      this.curPage = val;
    },
    next() {
      if (this.active++ > 4) this.active = 0;
    },
    /**/
    getParams() {
      let self = this;
      // 
      const params = this.$route.query.order_id;
      OrderApi.orderdetail(
        {
          order_id: params
        },
        true
      )
        .then(data => {
          self.loading = false;
          self.delivery_type = data.data.detail.delivery_type.value;
          self.tableData = [data.data.detail];
          self.address = [data.data.detail.address];
          self.productList = data.data.detail.product;
          self.extract_store = [data.data.detail.extract_store];
          self.express = [data.data.detail.express];
          self.expressList = data.data.expressList;
        })
        .catch(error => {});
    },
    /**/
    onSubmit() {
      let self = this;
      let param = self.form;
      let order_id = this.$route.query.order_id;
      OrderApi.delivery(
        {
          param: param,
          order_id: order_id
        },
        true
      )
        .then(data => {
          this.$message({
            message: '',
            type: 'success'
          });
          this.getParams();
        })
        .catch(error => {});
    },
    /**/
    confirmCancel() {
      let self = this;
      let order_id = this.$route.query.order_id;
      let is_cancel = self.forms.is_cancel;
      OrderApi.confirm(
        {
          order_id: order_id,
          is_cancel: is_cancel
        },
        true
      )
        .then(data => {
          console.log(data);
          this.$message({
            message: '',
            type: 'success'
          });
        })
        .catch(error => {});
      this.getParams();
    }
  }
};
</script>
<style></style>



