<template>
  <!--
       WuYuseng
      2019-10-26
      --
  -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="200px">
      <!---->
      <div class="common-form"></div>
      <el-form-item label="" :rules="[{required: true,message: ''}]" prop="express_name">
        <el-input v-model="form.express_name" class="max-w460"></el-input>
        <div class="tips">
          
          <el-link href="/" target="_blank" type="primary" :underline="false"></el-link>
          
        </div>
      </el-form-item>
      <el-form-item label="" :rules="[{required: true,message: ''}]" prop="express_code">
        <el-input v-model="form.express_code" class="max-w460"></el-input>
        <div class="tips">100API</div>
      </el-form-item>
      <el-form-item label="" :rules="[{required: true,message: ''}]" prop="sort">
        <el-input v-model="form.sort" type="number" class="max-w460"></el-input>
        <div class="tips"></div>
      </el-form-item>

      <!---->
      <div class="common-button-wrapper">
        <el-button type="primary" @click="onSubmit" :loading="loading"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import SettingApi from '@/api/setting.js';

  export default {
    data() {
      return {
        /**/
        // activeIndex: '1',
        /*form*/
        form: {
          express_id: '',
          express_name: '',
          express_code: '',
          sort: ''
        },
        loading: false
      };
    },
    created() {
      this.getData();
    },

    methods: {
      getData() {
        let self = this;
        // 
        const params = this.$route.query.express_id;
        SettingApi.expressDetail({
              express_id: params
            },
            true
          )
          .then(data => {
            let detail = data.data.detail;
            // 
            self.form.express_id = detail.express_id;
            self.form.express_name = detail.express_name;
            self.form.express_code = detail.express_code;
            self.form.sort = detail.sort;
          })
          .catch(error => {});
      },
      //
      onSubmit() {
        let self = this;
        let params = this.form;
        self.$refs.form.validate((valid) => {
          if (valid) {
            self.loading = true;
            SettingApi.editExpress(params, true)
              .then(data => {
                self.loading = false;
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.$router.push('/setting/express/index');
              })
              .catch(error => {
                self.loading = false;
              });
          }
        });
      }
    }
  };
</script>

<style>
  .tips {
    color: #ccc;
  }
</style>



