<script>
import SettingApi from '@/api/setting.js';
export default {
  data() {
    return {
      tableData: [],
      loading: true
    };
  },
  created() {
    /**/
    //this.getData();
  },
  methods: {
    /**/
    getData() {
      let self = this;
      let Params = {};
      SettingApi.getCompany(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.data;
        })
        .catch(error => {});
    }
  }
};
</script>
<style>
  .express-company table{ width: 100%; border-collapse: collapse;}
  .express-company table th,.express-company table td{ padding: 12px; border: 1px solid #EBEEF5; text-align: left;}
</style>



