    	diy 
    -->
  <div>
    <div class="common-form">
      <span>{{ curItem.name }}</span>
    </div>
    <el-form size="small" :model="curItem" label-width="100px">
      <!---->
      <el-form-item label=""><el-slider v-model="curItem.style.bottom" show-input></el-slider></el-form-item>
      <!---->
      <el-form-item label=""><el-slider v-model="curItem.style.right" show-input></el-slider></el-form-item>
      <!---->
      <el-form-item label=""><el-slider v-model="curItem.style.opacity" show-input></el-slider></el-form-item>
      <!---->
      <el-form-item label="">
        <el-radio-group v-model="curItem.params.type">
            <el-radio :label="'chat'"></el-radio>
            <el-radio :label="'phone'"></el-radio>
          </el-radio-group>
      </el-form-item>
      <el-form-item label="" v-show="curItem.params.type == 'phone'">
        <el-input v-model="curItem.params.phone_num"></el-input>
      </el-form-item>
      <!---->
      <el-form-item label="">
        <div class="diy-service-icon">
          <img v-img-url="curItem.params.image" alt="" @click="$parent.onEditorSelectImage(curItem.params, 'poster')">
        </div>
        <div>9090</div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {

      };
    },
    props: ['curItem', 'selectedIndex', 'opts'],
    methods: {

    }
  };
</script>

<style>
.diy-service-icon, .diy-service-icon img{ width: 40px; height: 40px;}
</style>



