<template>
  <!--
          luoyiming
          2020-01-08
          
      -->
  <el-dialog title="" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false" :close-on-press-escape="false">
    <!---->
    <el-tabs type="border-card" v-model="activeName">
      <!---->
      <el-tab-pane label="" name="pages"><Pages v-if="activeName == 'pages'" @changeData="activeDataFunc"></Pages></el-tab-pane>
      <el-tab-pane label="" name="product"><Product v-if="activeName == 'product'" @changeData="activeDataFunc"></Product></el-tab-pane>
    </el-tabs>
    <div slot="footer" class="dialog-footer d-b-c">
      <div>
        
        <template v-if="activeData != null">
          {{ activeData.type + '   ' + activeData.name + '   ' + activeData.url }}
        </template>
        <template v-else>
          
        </template>
      </div>
      <div>
        <el-button size="small" @click="dialogFormVisible(false)"> </el-button>
        <el-button size="small" type="primary" @click="dialogFormVisible(true)"> </el-button>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import Pages from './part/Pages.vue';
import Product from './part/Product.vue';
export default {
  components: {
    Pages,
    Product
  },
  data() {
    return {
      /**/
      dialogVisible: true,
      /**/
      activeData: null,
      activeName: 'pages'
    };
  },
  props: ['is_linkset'],
  created() {
    this.dialogVisible = this.is_linkset;
  },
  methods: {
    /**/
    dialogFormVisible(e) {
      if (e) {
        console.log(this.activeData);
        this.$emit('closeDialog', this.activeData);
      } else {
        this.$emit('closeDialog', {url:null,type:'',name:''});
      }
    },

    /**/
    activeDataFunc(e) {
      this.activeData = e;
    }
  }
};
</script>

<style>
.marketing-box .el-tabs__item {
  font-size: 12px;
}
</style>



