<template>
  <!--
      luoyiming
      2019-10-25
      -
  -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" :rules="rules" label-width="150px">
      <!---->
      <div class="common-form"></div>
      <el-form-item label="" prop="name"><el-input v-model="form.name" placeholder="" class="max-w460"></el-input></el-form-item>
      <div class="common-form">api</div>
      <el-form-item label="100 Customer" prop="customer">
        <el-input v-model="form.customer" placeholder="" class="max-w460"></el-input>
        <div class="tips">
          ,
          <el-link :underline="false" href="https://www.kuaidi100.com/openapi/" target="_blank" type="primary">100</el-link>
        </div>
      </el-form-item>
      <el-form-item label="100 Key" prop="key"><el-input v-model="form.key" placeholder="" class="max-w460"></el-input></el-form-item>

      <!---->
      <div class="common-button-wrapper"><el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button></div>
    </el-form>
  </div>
</template>

<script>
import SettingApi from '@/api/setting.js';

export default {
  data() {
    return {
      /**/
      loading: false,
      /*form*/
      form: {
        name: '',
        customer: '',
        key: '',
        checkedCities: [
          // 10, 20
        ]
      },
      all_type: [],
      type: [],
      /**/
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        customer: [{ required: true, message: 'api', trigger: 'blur' }],
        key: [{ required: true, message: '100Key', trigger: 'blur' }],
      }
    };
  },
  created() {
    this.getParams();
  },

  methods: {
    /**/
    getParams() {
      let self = this;
      SettingApi.storeDetail({}, true)
        .then(data => {
          if (data.code == 1) {
            self.form.checkedCities = data.data.vars.values.delivery_type;
            let vars = data.data.vars.values;
            self.form.name = vars.name;
            self.type = vars.delivery_type;
            self.form.customer = vars.kuaidi100.customer;
            self.form.key = vars.kuaidi100.key;
            self.all_type = data.data.all_type;
          } else {
            self.$message.error('');
          }
        })
        .catch(error => {});
    },

    /**/
    onSubmit() {
      let self = this;
      let params = this.form;
      let form = self.form;
      self.$refs.form.validate(valid => {
        if (valid) {
          self.loading = true;
          SettingApi.editStore(params, true)
            .then(data => {
              self.loading = false;
              if (data.code == 1) {
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.$router.push('/setting/store');
              } else {
                self.$message.error('');
              }
            })
            .catch(error => {
              self.loading = false;
            });
        }
      });
    }
  }
};
</script>
<style>
.tips {
  color: #ccc;
}
</style>



