<template>
  <!--
      luoyiming
      2019-10-25
      
  -->
  <div class="user">
    <!---->
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="formInline.order_no" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="formInline.type" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option label="" value="10"></el-option>
            <el-option label="" value="20"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div class="block">
            <span class="demonstration"></span>
            <el-date-picker v-model="formInline.create_time" type="daterange" range-separator="" start-placeholder=""
              end-placeholder="">
            </el-date-picker>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search" type="primary" @click="onSubmit()"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="" name="-1"></el-tab-pane>
          <el-tab-pane :label="'('+(countArr.wait_count)+')'" name="0"></el-tab-pane>
          <el-tab-pane :label="''" name="10"></el-tab-pane>
          <el-tab-pane :label="''" name="20"></el-tab-pane>
          <el-tab-pane :label="''" name="30"></el-tab-pane>
        </el-tabs>
        <el-table size="small" :data="tableData" border style="width: 100%" v-loading="loading">
          <el-table-column prop="order_no" label="" width="130"></el-table-column>
          <el-table-column prop="orderproduct.product_name" width="400" label="">
            <template slot-scope="scope">
              <div class="d-s-c">
                <img v-img-url="scope.row.orderproduct.image.file_path" alt="" width="50px" />
                <p class="ml10">{{scope.row.orderproduct.product_name}}</p>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label="" width="150" ></el-table-column>
          <el-table-column prop="orderproduct.product_price" label="">
            <template slot-scope="scope">
              <span class="orange">{{scope.row.orderproduct.product_price}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="orderproduct.total_num" label=""></el-table-column>
          <el-table-column prop="orderproduct.total_pay_price" label=""></el-table-column>
          <el-table-column prop="user.nickName" label="">
            <template slot-scope="scope">
              <span>{{scope.row.user.nickName}}</span><br>
              <span class="gray9">(ID:{{scope.row.user.user_id}})</span>
            </template>
          </el-table-column>
          <el-table-column prop="type.text" label="">
            <template slot-scope="scope">
              <span class="orange">{{scope.row.type.text}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="" label="">
            <template slot-scope="scope">
              <div v-if="scope.row.status.value == 0">
                <!-- -->
                <p>
                  <span class="orange" v-if="scope.row.is_agree.value==0">{{scope.row.is_agree.text}}</span>
                  <span class="orange" v-if="scope.row.is_agree.value==10">{{scope.row.is_agree.text}}</span>
                  <span class="orange" v-if="scope.row.is_agree.value==20">{{scope.row.is_agree.text}}</span>
                </p>
                <!-- -->
                <p v-if="scope.row.type.value==10 && scope.row.is_agree.value==10">
                  <span class="orange" v-if="scope.row.is_user_send == 0"></span>
                  <span class="orange" v-else></span>
                </p>
                <!-- -->
                <p v-if="scope.row.type.value==10 && scope.row.is_agree.value==10 && scope.row.is_user_send==1 &&scope.row.is_receipt==0 ">
                  <span></span> <span class="orange"></span></p>
              </div>
              <div v-if="scope.row.status.value==20">
                <span>{{scope.row.status.text}} </span>
              </div>
              <div v-if="scope.row.status.value==10">
                <span>{{scope.row.status.text}} </span>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="orderMaster.create_time" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="150">
            <template slot-scope="scope">
              <p>
              <el-button @click="Detail(scope.row.order_refund_id)" type="text" size="small"></el-button>
              </p>
              <p>
              <el-button v-if="scope.row.is_agree.value==0" @click="Detail(scope.row.order_refund_id)" type="text" size="small"></el-button>
              </p>
              <p>
              <el-button v-if="scope.row.type.value==10 && scope.row.is_agree.value==10 && scope.row.is_receipt==0 && scope.row.is_user_send==1"
                @click="Detail(scope.row.order_refund_id)" type="text" size="small">
              </el-button>
              </p>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background :current-page="curPage"
          :page-size="pageSize" layout="total, prev, pager, next, jumper"
          :total="totalDataNumber">
        </el-pagination>
      </div>
    </div>


  </div>
</template>

<script>
  import OrderApi from '@/api/order.js';
  export default {
    data() {
      return {
        /**/
        loading: true,
        /**/
        tableData: [],
        /**/
        pageSize: 20,
        /**/
        totalDataNumber: 0,
        /**/
        curPage: 1,
        /**/
        formInline: {
          order_no: '',
          style_id: '',
          shop_id: '',
          create_time: '',
        },
        status: -1,
        countArr: [],
        activeName: '-1',
      };
    },
    created() {
      /**/
      this.getTableList();
    },
    methods: {
      /**/
      handleCurrentChange(val) {
        let self = this;
        self.curPage = val;
        self.loading = true;
        this.getTableList();
      },

      /**/
      handleSizeChange(val) {
        this.curPage = 1;
        this.pageSize = val;
        this.getTableList();
      },
      /**/
      handleClick(tab, event) {
        let self = this;
        self.curPage = 1;
        self.loading = true;
        self.status = tab.name;
        self.getTableList();
      },
      /**/
      getTableList(params = '') {
        let self = this;
        let Params = {};
        Params.status = self.status;
        Params.page = self.curPage;
        Params.list_rows = self.pageSize;
        if (params != '') {
          Params.order_no = params.order_no;
          Params.create_time =  params.create_time;
          Params.type =  params.type;
        }
        OrderApi.orderrefund(Params, true).then(data => {
          self.loading = false;
          self.tableData = data.data.list.data;
          self.totalDataNumber = data.data.list.total;
          self.exStyle = data.data.ex_style;
          self.shopList = data.data.shopList;
          self.countArr = data.data.countArr;
        }).catch(error => {

        });
      },

      /**/
      onSubmit() {
        let self = this;
        let Params = this.formInline;
        self.getTableList(Params);
      },

      /**/
      Detail(e) {
        let self = this;
        let order_refund_id = e;
        this.$router.push({
          path: '/order/refund/Detail',
          query: {
            order_refund_id: order_refund_id
          }
        })
      }
    }
  };
</script>
<style></style>



