<template>
  <!--
    	wangxw
    	2019-10-26
    	-
    -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" label-width="200px">
      <!---->
      <div class="common-form"></div>

      <el-form-item label="">
        <div>
          <el-radio v-model="form.radio" label="local" @change="getRadio"> ()</el-radio>
          <el-radio v-model="form.radio" label="qiniu" @change="getRadio"></el-radio>
          <el-radio v-model="form.radio" label="aliyun" @change="getRadio">OSS</el-radio>
          <el-radio v-model="form.radio" label="qcloud" @change="getRadio">COS</el-radio>
        </div>
      </el-form-item>
      <!---->
      <div v-if="form.radio == 'qiniu'">
        <el-form-item label=" Bucket" :rules="[{ required: true, message: ' Bucket' }]" prop="engine.qiniu.bucket">
          <el-input v-model="form.engine.qiniu.bucket" class="max-w460"></el-input>
        </el-form-item>
        <el-form-item label="ACCESS_KEY AK" :rules="[{ required: true, message: 'ACCESS_KEY AK' }]" prop="engine.qiniu.access_key">
          <el-input v-model="form.engine.qiniu.access_key" class="max-w460"></el-input>
        </el-form-item>
        <el-form-item label="SECRET_KEY SK" :rules="[{ required: true, message: 'SECRET_KEY SK' }]" prop="engine.qiniu.secret_key">
          <el-input v-model="form.engine.qiniu.secret_key" class="max-w460"></el-input>
        </el-form-item>
        <el-form-item label=" Domain" :rules="[{ required: true, message: ' Domain' }]" prop="engine.qiniu.domain">
          <el-input v-model="form.engine.qiniu.domain" class="max-w460"></el-input>
          <div class="tips">http://  https://http://static.cloud.com</div>
        </el-form-item>
      </div>
      <!--OSS-->
      <div v-if="form.radio == 'aliyun'">
        <el-form-item label=" Bucket" :rules="[{ required: true, message: ' Bucket' }]" prop="engine.aliyun.bucket">
          <el-input v-model="form.engine.aliyun.bucket" class="max-w460"></el-input>
        </el-form-item>
        <el-form-item label="AccessKeyId" :rules="[{ required: true, message: 'AccessKeyId' }]" prop="engine.aliyun.access_key_id">
          <el-input v-model="form.engine.aliyun.access_key_id" class="max-w460"></el-input>
        </el-form-item>
        <el-form-item label="AccessKeySecret" :rules="[{ required: true, message: 'AccessKeySecret' }]" prop="engine.aliyun.access_key_secret">
          <el-input v-model="form.engine.aliyun.access_key_secret" class="max-w460"></el-input>
        </el-form-item>
        <el-form-item label=" Domain" :rules="[{ required: true, message: ' Domain' }]" prop="engine.aliyun.domain">
          <el-input v-model="form.engine.aliyun.domain" class="max-w460"></el-input>
          <div class="tips">http://  https://http://static.cloud.com</div>
        </el-form-item>
      </div>
      <!--COS-->
      <div v-if="form.radio == 'qcloud'">
        <el-form-item label=" Bucket" :rules="[{required: true,message: ' Bucket'}]" prop="engine.qcloud.bucket">
          <el-input v-model="form.engine.qcloud.bucket" class="max-w460"></el-input>
         </el-form-item>
        <el-form-item label=" Region" :rules="[{required: true,message: ' Region'}]" prop="engine.qcloud.region">
          <el-input v-model="form.engine.qcloud.region" class="max-w460"></el-input>
          <div class="tips">ap-beijingap-hongkongeu-frankfurt</div>
        </el-form-item>
        <el-form-item label="SecretId" :rules="[{required: true,message: 'SecretId'}]" prop="engine.qcloud.secret_id">
          <el-input v-model="form.engine.qcloud.secret_id" class="max-w460"></el-input>
         </el-form-item>
        <el-form-item label="SecretKey" :rules="[{required: true,message: 'SecretKey'}]" prop="engine.qcloud.secret_key">
          <el-input v-model="form.engine.qcloud.secret_key" class="max-w460"></el-input>
        </el-form-item>
        <el-form-item label=" Domain" :rules="[{required: true,message: ' Domain'}]" prop="engine.qcloud.domain">
          <el-input v-model="form.engine.qcloud.domain" class="max-w460"></el-input>
          <div class="tips">http://  https://http://static.cloud.com</div>
        </el-form-item>
      </div>

      <!---->
      <div class="common-button-wrapper"><el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button></div>
    </el-form>
  </div>
</template>

<script>
import SettingApi from '@/api/setting.js';

export default {
  data() {
    return {
      /**/
      // activeIndex: '1',
      /*form*/
      form: {
        radio: 'local',
        engine: {
          qiniu: {
            // domain: '',
            // access_key: '',
            // secret_key: '',
            // bucket: '',
          },
          aliyun: {
            // domain: '',
            // access_key_id: '',
            // access_key_secret: '',
            // bucket: '',
          },
          qcloud: {
            // domain: '',
            // region: '',
            // secret_id: '',
            // secret_key: '',
            // bucket: '',
          }
        }
      },
      loading: false
    };
  },
  created() {
    this.getData();
  },

  methods: {
    getData() {
      let self = this;
      SettingApi.storageDetail({}, true)
        .then(data => {
          let vars = data.data.vars.values;
          self.form.radio = vars.default;
          self.form.engine.qiniu = vars.engine.qiniu;
          self.form.engine.aliyun = vars.engine.aliyun;
          self.form.engine.qcloud = vars.engine.qcloud;
        })
        .catch(error => {});
    },
    //
    onSubmit() {
      let self = this;
      self.$refs.form.validate((valid) => {
        if (valid) {
          self.loading = true;
          let params = this.form;
          SettingApi.editStorage(params, true)
            .then(data => {
              self.loading = false;
              self.$message({
                message: '',
                type: 'success'
              });
              // self.$router.push('/setting/Storage');
            })
            .catch(error => {
              self.loading = false;
            });
        }
     });

    },
    //
    getRadio(val) {}
  }
};
</script>

<style>
.tips {
  color: #ccc;
}
</style>



