<template>
  <!--
          luoyiming
          2019-11-13
          
      -->
  <el-dialog title="" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false" :close-on-press-escape="false" width="30%">
    <el-form size="small" :model="formData" :rules="rules" ref="formData">
      <!---->
      <el-form-item label="" :label-width="formLabelWidth" prop="oldpass">
        <el-input type="password" v-model="formData.oldpass" autocomplete="off"></el-input>
      </el-form-item>
      <!---->
      <el-form-item label="" :label-width="formLabelWidth" prop="password">
        <el-input type="password" v-model="formData.password" autocomplete="off"></el-input>
      </el-form-item>
      <!---->
      <el-form-item label="" :label-width="formLabelWidth" prop="confirmPass">
        <el-input type="password" v-model="formData.confirmPass" autocomplete="off"></el-input>
      </el-form-item>
    </el-form>

    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible"> </el-button>
      <el-button type="primary" @click="submitFunc('formData')" :loading="loading"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import UserApi from '@/api/user.js';
export default {
  data() {

    /**/
    var validatePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.formData.confirmPass !== '') {
          this.$refs.formData.validateField('confirmPass');
        }
        callback();
      }
    };
    /**/
    var validateConfirmPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.formData.password) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };

    return {
      loading: false,
      /**/
      formLabelWidth: '100px',
      /**/
      dialogVisible: true,
      /**/
      formData: {
         /**/
        oldpass:'',
        /**/
        password:'',
        /**/
        confirmPass:''
      },
      /**/
      rules: {
        /**/
        oldpass: [{ required: true, message: '', trigger: 'blur' }],
        /**/
        password: [{ required: true, validator:validatePassword, trigger:'blur' }],
        /**/
        confirmPass: [{ required: true, validator:validateConfirmPass, trigger:'blur' }]
      }
    };
  },
  methods: {

    /**/
    submitFunc(formData) {
      let self = this;
      let Param = self.formData;
      self.$refs[formData].validate(valid => {
        /**/
        if (valid) {
          self.loading = true;
          UserApi.EditPass(Param, true)
            .then(data => {
              self.loading = false;
              /**/
              if (data.code == 1) {
                self.$message({
                  message: data.msg,
                  type: 'success'
                });
                self.dialogFormVisible();
              } else {
                self.loading = false;
              }
            })
            .catch(error => {
              self.loading = false;
            });
        }
      });
    },

    /**/
    dialogFormVisible() {
      this.$emit('close', false);
    }
  }
};
</script>

<style></style>



