<template>
  <!--
    luoyiming
    2019-10-25
    -
  -->
  <div class="product-add">
    <!--form-->
    <el-form size="small" ref="form" :model="form" :rules="rules" label-width="150px">
      <!---->
      <div class="common-form"></div>
      <el-form-item label=""  prop="close_days">
        <el-input v-model="form.close_days" type="number" style="width: 200px;"></el-input>
        <span class="ml10"></span>
        <p class="tips">n0</p>
      </el-form-item>
      <el-form-item label="" prop="receive_days">
        <el-input v-model="form.receive_days" type="number" style="width: 200px;"></el-input>
        <span class="ml10"></span>
        <p class="tips">0</p>
      </el-form-item>
      <el-form-item label="" prop="refund_days">
        <el-input v-model="form.refund_days" type="number" style="width: 200px;"></el-input>
        <span class="ml10"></span>
        <p class="tips"> n0</p>
      </el-form-item>

      <div class="common-form"></div>
      <el-form-item label="" :rules="[{ required: true, message: ' ' }]" prop="freight_rule">
        <div>
          <el-radio v-model="form.freight_rule" :label="10"></el-radio>
          <p class="tips"></p>
        </div>
        <div class="mt10">
          <el-radio v-model="form.freight_rule" :label="20"></el-radio>
          <p class="tips"></p>
        </div>
        <div class="mt10">
          <el-radio v-model="form.freight_rule" :label="30"></el-radio>
          <p class="tips"></p>
        </div>
      </el-form-item>

      <!---->
      <div class="common-button-wrapper"><el-button size="small" type="primary" @click="onSubmit" :loading="loading"></el-button></div>
    </el-form>
  </div>
</template>

<script>
import SettingApi from '@/api/setting.js';

export default {
  data() {
    return {
      /**/
      // activeIndex: '1',
      /*form*/
      form: {
        close_days: '',
        receive_days: '',
        refund_days: '',
        freight_rule: ''
      },
      loading: false,
      /**/
      rules: {
        close_days: [{ required: true, message: '', trigger: 'blur' }],
        receive_days: [{ required: true, message: '', trigger: 'blur' }],
        refund_days: [{ required: true, message: '', trigger: 'blur' }],
      }
    };
  },
  created() {
    this.getParams();
  },

  methods: {
    getParams() {
      let self = this;
      SettingApi.tradeDetail({}, true)
        .then(data => {
          if (data.code == 1) {
            let vars = data.data.vars.values;
            self.form.close_days = vars.order.close_days;
            self.form.receive_days = vars.order.receive_days;
            self.form.refund_days = vars.order.refund_days;
            self.form.freight_rule = vars.freight_rule;
          }
        })
        .catch(error => {});
    },

    //
    handleCheckedCitiesChange(val) {},

    /**/
    onSubmit() {
      let self = this;
      let form = self.form;
      self.$refs.form.validate(valid => {
        if (valid) {
          self.loading = true;
          SettingApi.editTrade(form, true)
            .then(data => {
              self.loading = false;
              if (data.code == 1) {
                self.$message({
                  message: '',
                  type: 'success'
                });
                self.$router.push('/setting/Trade');
              } else {
                self.loading = false;
              }
            })
            .catch(error => {
              self.loading = false;
            });
        }
      });
    }
  }
};
</script>

<style>
.tips {
  color: #ccc;
}
</style>



