/**/
let baserouter = [
    /**/
    {
        path: '/login',
        name: 'Login',
        meta: {
            title: ''
        },
        component: () =>
            import('@/views/Login')
    },
    {
        path: '/',
        redirect: {
            name: 'Home'
        },
        meta: {
            title: ''
        },
        component: () =>
            import('@/views/layout/Main'),
        children: [
            /**/
            {
                path: '/home',
                name: 'Home',
                meta: {
                    title: ''
                },
                component: () =>
                    import('@/views/home/Home')
            },
        ]
    },
    /**/
    {
        path: '/test',
        name: 'Test',
        meta: {
            title: ''
        },
        component: () =>
            import('@/views/help/Test')
    },
    /**/
    {
        path: '/fonticon',
        name: 'Fonticon',
        meta: {
            title: ''
        },
        component: () =>
            import('@/views/help/Fonticon')
    }
];

/**/
export const errpage = [{
  path: '*',
  name: 'Page404',
  meta: {
    title: ''
  },
  component: () =>
    import('@/views/error-page/404')
}]

export default baserouter;



