<template>
  <!--
      luoyiming
      2019-10-25
      
  -->
  <div class="user">
    <!---->
    <div class="common-seach-wrap">
      <el-form size="small" :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label=""><el-input size="small" v-model="searchForm.order_no" placeholder=""></el-input></el-form-item>
        <el-form-item label="">
          <div class="block">
            <span class="demonstration"></span>
            <el-date-picker
              size="small"
              v-model="searchForm.create_time"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
            ></el-date-picker>
          </div>
        </el-form-item>
        <el-form-item><el-button size="small" type="primary" icon="el-icon-search" @click="onSubmit"></el-button></el-form-item>
      </el-form>
    </div>
    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="" name="all"></el-tab-pane>
          <el-tab-pane :label="'(' + order_count.payment + ')'" name="payment"></el-tab-pane>
          <el-tab-pane :label="'(' + order_count.delivery + ')'" name="delivery"></el-tab-pane>
          <el-tab-pane :label="'(' + order_count.received + ')'" name="received"></el-tab-pane>
          <el-tab-pane label="" name="comment"></el-tab-pane>
          <el-tab-pane label="" name="complete"></el-tab-pane>
        </el-tabs>
        <el-table size="small" :data="tableData.data" border :span-method="arraySpanMethod" style="width: 100%" v-loading="loading">
          <el-table-column prop="order_no" label="" width="600">
            <template slot-scope="scope">
              <div class="order-code" v-if="scope.row.is_top_row">
                <span class="c_main">{{ scope.row.order_no }}</span>
                <span class="pl16">{{ scope.row.create_time }}</span>
              </div>
              <template v-else>
                <div class="product-info" v-for="(item, index) in scope.row.product" :key="index">
                  <div class="pic"><img v-img-url="item.image.file_path" alt="" /></div>
                  <div class="info">
                    <div class="name gray3">{{ item.product_name }}</div>
                    <div class="gray9" v-if="item.product_attr">{{ item.product_attr }}</div>
                  </div>
                  <div class="d-c-c d-c">
                    <div class="orange"> {{ item.product_price }}</div>
                    <div class="gray3">x{{ item.total_num }}</div>
                  </div>
                </div>
              </template>
            </template>
          </el-table-column>
          <el-table-column prop="pay_price" label="(:)">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              <div class="orange">{{ scope.row.pay_price }}</div>
              <p class="gray9">({{ scope.row.express_price }})</p>
            </template>
          </el-table-column>
          <el-table-column prop="" label="">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              <div>{{ scope.row.user.nickName }}</div>
              <div class="gray9">ID({{ scope.row.user.user_id }})</div>
            </template>
          </el-table-column>
          <el-table-column prop="state_text" label="">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              {{ scope.row.state_text }}
            </template>
          </el-table-column>
          <el-table-column prop="is_comment" label="" width="120">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              <span v-if="scope.row.is_comment == 0"></span>
              <span v-else=""></span>
            </template>
          </el-table-column>
          <el-table-column fixed="right" label="" width="130">
            <template slot-scope="scope" v-if="!scope.row.is_top_row">
              <el-button @click="addClick(scope.row)" type="text" size="small" v-auth="'/order/order/detail'"></el-button>
              <el-button
                v-if="scope.row.pay_status.value == 20 && scope.row.delivery_status.value == 10 && scope.row.order_status.value != 20 && scope.row.order_status.value != 21"
                @click="addClick(scope.row)"
                type="text"
                size="small"
              >
                
              </el-button>
              <el-button v-if="scope.row.order_status.value == 21" @click="addClick(scope.row)" type="text" size="small"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!---->
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          background
          :current-page="curPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="totalDataNumber"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import OrderApi from '@/api/order.js';
export default {
  data() {
    return {
      type: 'all',
      /**/
      activeName: 'all',
      /**/
      loading: true,
      /**/
      tableData: {},
      /**/
      pageSize: 20,
      /**/
      totalDataNumber: 0,
      /**/
      curPage: 1,
      /**/
      searchForm: {
        order_no: '',
        create_time: ''
      },
      /**/
      create_time: '',
      /**/
      order_count: {
        payment: 0,
        delivery: 0,
        received: 0
      }
    };
  },
  created() {
    /**/
    this.getData();
  },
  methods: {
    /**/
    arraySpanMethod(row) {
      if (row.rowIndex % 2 == 0) {
        if (row.columnIndex === 0) {
          return [1, 6];
        }
      }
    },

    /**/
    handleCurrentChange(val) {
      let self = this;
      self.curPage = val;
      self.loading = true;
      self.getData();
    },

    /**/
    handleSizeChange(val) {
      this.curPage = 1;
      this.pageSize = val;
      this.getData();
    },

    /**/
    handleClick(tab, event) {
      let self = this;
      self.curPage = 1;
      self.loading = true;
      self.type = tab.name;
      self.getData();
    },
    /**/
    getData() {
      let self = this;
      let Params = {};
      Params.type = self.type;
      Params.page = self.curPage;
      Params.list_rows = self.pageSize;
      OrderApi.orderlist(Params, true)
        .then(res => {
          self.loading = false;
          let list = [];
          for (let i = 0; i < res.data.list.data.length; i++) {
            let item = res.data.list.data[i];
            let topitem = {
              order_no: item.order_no,
              create_time: item.create_time,
              is_top_row: true
            };
            list.push(topitem);
            list.push(item);
          }

          self.tableData.data = list;
          self.totalDataNumber = res.data.list.total;
          self.order_count = res.data.order_count.order_count;
        })
        .catch(error => {});
    },
    /**/
    addClick(row) {
      let self = this;
      let params = row.order_id;
      this.$router.push({
        path: '/order/order/Detail',
        query: {
          order_id: params
        }
      });
    },
    /**/
    onSubmit() {
      let self = this;
      let Params = this.searchForm;
      Params.name = self.name;
      Params.page = self.curPage;
      Params.list_rows = self.pageSize;
      self.loading = true;
      OrderApi.orderlist(Params, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.list;
          self.totalDataNumber = data.data.list.total;
        })
        .catch(error => {});
    }
  }
};
</script>
<style >
.product-info {
  padding: 10px 0;
  border-top: 1px solid #eeeeee;
}
.table-wrap .product-info:first-of-type {
  border-top: none;
}
.table-wrap .el-table__body tbody .el-table__row:nth-child(odd){ background: #f5f7fa;}
</style>



