/**/
const common={

	namespaced: true,

	/**/
	state: {
		is_show: false,
		page: null
	},

	/**/
	getters: {
		getTset: (state) => (name) => {
			return state.test + name;
		}
	},

	/* */
	mutations: {
		setState(state, value) {
			state[value.key]=value.val;
		}
	},

	/**/
	actions:{
		saveTest(context) {
			context.commit('saveTest')
		}
	}
}
export default common;



