<template>
  <!--
    	wangxw
    	2019-10-26
    	
    -->
  <div class="product">
    <!---->
    <div class="common-level-rail">
      <el-button size="small" type="primary" icon="el-icon-plus" @click="addClick" v-auth="'/product/category/add'"></el-button>
      <el-button size="small" @click="gotoPageCategory" v-auth="'/product/category/add'"></el-button>
    </div>

    <!---->
    <div class="product-content">
      <div class="table-wrap">
        <el-table size="mini" :data="tableData" row-key="category_id" default-expand-all :tree-props="{ children: 'child' }" style="width: 100%">
          <el-table-column prop="name" label="" width="180"></el-table-column>
          <el-table-column prop="" label="" width="180">
            <template slot-scope="scope">
              <img class="category-photo" v-img-url="hasImages(scope.row.images)" alt="" />
            </template>
          </el-table-column>
          <el-table-column prop="sort" label=""></el-table-column>
          <el-table-column prop="create_time" label=""></el-table-column>
          <el-table-column fixed="right" label="" width="100">
            <template slot-scope="scope">
              <el-button @click="editClick(scope.row)" type="text" size="small" v-auth="'/product/category/edit'"></el-button>
              <el-button @click="deleteClick(scope.row)" type="text" size="small" v-auth="'/product/category/delete'"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <!---->
    <Add v-if="open_add" :open_add="open_add" :addform="categoryModel" @closeDialog="closeDialogFunc($event, 'add')"></Add>
    <!---->
    <Edit v-if="open_edit" :open_edit="open_edit" :editform="categoryModel" @closeDialog="closeDialogFunc($event, 'edit')"></Edit>
  </div>
</template>

<script>
import PorductApi from '@/api/product.js';
import Add from './Add.vue';
import Edit from './Edit.vue';
export default {
  components: {
    Add,
    Edit
  },
  data() {
    return {
      /**/
      loading: true,
      /**/
      tableData: [],
      /**/
      open_add: false,
      /**/
      open_edit: false,
      /**/
      categoryModel: {
        catList: [],
        model: {}
      }
    };
  },
  created() {
    /**/
    this.getData();
  },
  methods: {
    hasImages(e) {
      if (e) {
        return e.file_path;
      } else {
        return '';
      }
    },

    /**/
    getData() {
      let self = this;
      PorductApi.catList({}, true)
        .then(data => {
          self.loading = false;
          self.tableData = data.data.list;
          self.categoryModel.catList = self.tableData;
        })
        .catch(error => {
          self.loading = false;
        });
    },

    /**/
    addClick() {
      this.open_add = true;
    },

    /**/
    editClick(item) {
      this.categoryModel.model = item;
      this.open_edit = true;
    },

    /**/
    closeDialogFunc(e, f) {
      if (f == 'add') {
        this.open_add = e.openDialog;
        if (e.type == 'success') {
          this.getData();
        }
      }
      if (f == 'edit') {
        this.open_edit = e.openDialog;
        if (e.type == 'success') {
          this.getData();
        }
      }
    },

    /**/
    deleteClick(row) {
      let self = this;
      self
        .$confirm('?', '', {
          type: 'warning'
        })
        .then(() => {
          PorductApi.catDel({
            category_id: row.category_id
          }).then(data => {
            self.$message({
              message: '',
              type: 'success'
            });
            self.getData();
          });
        });
    },

    /**/
    gotoPageCategory(){
      this.$router.push({
        path:'/page/category'
      })
    }

  }
};
</script>

<style scoped="">
.category-photo {
  width: 20px;
  height: 20px;
}
</style>



